.map-container {
    height: 600px;
    width : 1000px;
    margin: auto;
    position: relative;
}

.map {
    top: 0;
    bottom: 0;
    position: absolute;
    width: 100%;
    height: 100%;
}

.sidebar {
    background-color: rgba(35, 55, 75, 0.9);
    color: #fff;
    padding: 6px 12px;
    font-family: monospace;
    z-index: 1;
    position: absolute;
    top: 0;
    left: 0;
    margin: 12px;
    border-radius: 4px;
}